import React from 'react'
import { CChartLine } from '@coreui/react-chartjs'
import { getStyle, hexToRgba } from '@coreui/utils/src'

const brandSuccess = getStyle('success') || '#4dbd74'
const brandInfo = getStyle('info') || '#20a8d8'
const brandDanger = getStyle('danger') || '#f86c6b'

const MainChartExample = props => {
  const month = ["Jan", "Feb", "Mar", "Apr", "Mei", "Jun", "Jul", "Aug", "Sep", "Okt", "Nov", "Dec"];
  const totals = props.data.map(d => d.total);
  const max = Math.max(...totals);
  const l = props.data.map(d => d.month ?? d.date);
  const labels = l.map(d => {
    const x = d.split("-")
    if(x.length === 2){
      return `${month[x[1] - 1]} ${x[0]}`;
    } else {
      return `${x[2]} ${month[x[1] - 1]} ${x[0]}`;
    }
  });

  if(labels.length === 1 && totals.length === 1) {
    labels.unshift(0);
    totals.unshift(0);
  }

  const defaultDatasets = (()=>{
    return [
      {
        label: 'Kasus',
        backgroundColor: hexToRgba(brandInfo, 10),
        borderColor: brandInfo,
        pointHoverBackgroundColor: brandInfo,
        borderWidth: 2,
        data: totals,
      },
    ]
  })()

  const numberFormat = (v) => {
    if (v == null) return v;
    return v.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  const defaultOptions = (()=>{
    return {
        maintainAspectRatio: false,
        legend: {
          display: false
        },
        tooltips: {
          callbacks: {
            label: (tooltipItem, data) => {
              // console.log("tooltip", tooltipItem.datasetIndex);
              // console.log("data", data.datasets);
              const n = tooltipItem.datasetIndex;
              return data.datasets[n].label;
            },
            footer: (tooltipItems, data) => {
              const tooltip = tooltipItems[0];
              return (tooltip.value);
            },
          },
        },
        scales: {
          xAxes: [{
            gridLines: {
              drawOnChartArea: false
            }
          }],
          yAxes: [{
            ticks: {
              beginAtZero: true,
              maxTicksLimit: 10,
              stepSize: 1,
              max: Math.ceil(max * (120 / 100)),
              callback(v) {
                return (v);
              }
            },
            gridLines: {
              display: true
            }
          }]
        },
        elements: {
          point: {
            radius: 0,
            hitRadius: 10,
            hoverRadius: 4,
            hoverBorderWidth: 3
          }
        }
      }
    }
  )()

  // render
  return (
    <CChartLine
      {...props}
      datasets={defaultDatasets}
      options={defaultOptions}
      labels={labels}
    />
  )
}


export default MainChartExample