import axiosInstance from "./axiosInstance";

export async function createCyberTipline(data) {
  const formData = new FormData();
  for (let d in data) {
    if (d === "suspectDob" || d === "dateMadeAvailable" || d === "incidentDate")
      formData.append(d, new Date(data[d]).toUTCString());
    else if (d === "files") {
      for (let f of data.files) {
        formData.append("files", f);
      }
    } else formData.append(d, data[d]);
  }
  return await axiosInstance.post("/cyber-tipline", formData);
}

export async function searchCyberTipline(params) {
  return await axiosInstance.get("/cyber-tipline/search", { params });
}

export async function getCyberTipline(search = null, page = 0) {
  let qs = `?size=10&page=${page > 0 ? page - 1 : page}`;
  if (search) qs += `&search=${search}`;
  return await axiosInstance.get(`/cyber-tipline${qs}`);
}

export async function showCyberTipline(id) {
  return await axiosInstance.get(`/cyber-tipline/${id}`);
}

export async function deleteCyberTipline(id) {
  return await axiosInstance.delete(`/cyber-tipline/${id}`);
}

export async function updateCyberTipline(data) {
  return await axiosInstance.put("/cyber-tipline", data);
}

export async function updateCyberTiplineReport(report, id) {
  return await axiosInstance.put(`/cyber-tipline/report/${id}`, { report });
}

export async function getCyberTiplineDaily() {
  return await axiosInstance.get("/cyber-tipline/stat/daily");
}

export async function cyberTiplineGeneratePdf(id) {
  return await axiosInstance.get(`/cyber-tipline/pdf/${id}`, { responseType: "blob" });
}

export async function getCyberTiplineMonthly() {
  return await axiosInstance.get("/cyber-tipline/stat/monthly");
}

export async function getCyberTiplinePieChart() {
  return await axiosInstance.get("/cyber-tipline/stat/pie");
}

export async function assignCyberTiplineTo(param) {
  return await axiosInstance.put(`/cyber-tipline/assign-to/${param.ctId}/${param.uId}`);
}
